@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-weight:400;font-family:"Quicksand",sans-serif;font-size:16px;line-height:1.7;color:#616161;background-color:#ff8a80}a:link,a:visited{color:#e53935;text-decoration:none}a:hover,a:active{color:#ff8a80}button{background-color:transparent;border:none}span{display:inline !important}.wrapper{margin-right:auto;margin-left:auto;height:100%;max-width:1280px;padding-right:10px;padding-left:10px;display:flex;align-items:center;justify-content:center}.main{padding-top:20px;box-shadow:0px 13px 40px -13px rgba(0,0,0,0.75);width:98%;margin:0 auto;border-radius:3px;overflow:hidden;height:96vh}.main .row:after{content:"";display:table;clear:both}.header{text-align:center;border:transparent;background-color:#e53935;padding:2rem 3rem}.header__title{font-weight:600;color:white}.content{background-color:white;width:100%;height:100%;box-sizing:border-box}.date-time{margin-top:1rem;text-align:center;color:#e53935}.error{color:crimson;font-size:1.8rem;margin-top:3rem;text-align:center}.footer{padding:1.5rem 0;text-align:center;transform:translateX(-40%);border-top:solid 2px rgba(255,138,128,0.3);letter-spacing:0.3px}@media (max-width: 600px){.footer{letter-spacing:0;width:90%}}.map{width:70%;border:2px solid red;float:left}@media (max-width: 768px){.map{width:100%}}.sidebar{width:30%;border:2px solid green;max-height:80vh;float:right;overflow:auto}@media (max-width: 768px){.sidebar{width:100%;float:left}}.reviewarea{width:100%;border:2px solid green;max-height:80vh;float:right;overflow:scroll}@media (max-width: 768px){.reviewarea{width:100%;float:left}}.card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);transition:0.3s;width:100%}.card:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}.container{padding:2px 16px}.restaurant-image{object-fit:contain;min-width:100%;max-height:300px;width:auto;border:1px solid #223322;background-image:url("https://via.placeholder.com/150")}.restaurant-title{font-size:1.8rem;font-weight:bold}.restaurant-rating{font-size:1.6rem;font-weight:bold}.filter-rating{text-align:center;color:#e53935;padding-top:20px;padding-bottom:20px;display:flex;align-items:center;justify-content:center}.rating-text{font-size:1.8rem;margin-right:10px}

.addrest{
    z-index: 1;
    position: absolute;
    margin: 200px;
    height: auto;
    width: auto;
    background-color: azure;
}

button, input {
    overflow: visible;
    width: 70%;
}
/* .popup-content {
    width: auto !important;
} */
.popupClass{
    display: flex;
    flex-direction: row;
}
.ReviewNameRating{
    display: block;
}
.modal {
    font-size: 12px;
  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .popupClass {
    width: 100%;
    padding: 10px 5px;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }
